/* HB Version 2 */
const boot = (params) => {
  return new Promise(async (resolve) => {
    let app = params.app
    let globalData = params.globalData
    if (typeof params.globalData == 'object') bindGlobalData(app, globalData)
    if (typeof params.beforeBoot == 'function') await params.beforeBoot(globalData)

    await $store.dispatch('App-App-state/doneLoading')
    resolve({
      globalData
    })
  })
}

const bindGlobalData = (app, bindGlobally) => {
  let gp = app.config.globalProperties
  for (let e in bindGlobally) {
    window[e] = bindGlobally[e]
    gp[e] = bindGlobally[e]
  }
}

export default boot
