export const ml={lang:null,langs:{},getFrom(n,t,s){return this.langs[n][t]||s||console.error("Devegram ML: Missing Translations for ",t,"in "+n+" language")||null},get:function(n,t){return this.langs[this.lang][n]||t||console.error("Devegram ML: Missing Translations for ",n)||null},hasLang:function(n){return this.langs.hasOwnProperty(n)},hasTrans:function(n){return this.langs[this.lang].hasOwnProperty(n)},addLang:function(n,t){n.constructor===Array?n.forEach(n=>{this.langs[n.lang]=n.translation}):this.langs[n]=t},setLang:function(n){this.lang=n},getLangs:function(){return Object.keys(this.langs)},getLang:function(n){return this.langs[n]}};export default ml;