import {getLS} from "devegram-javascript-util";

export const hkms = () => {
  return {
    store: null,
    router: null,
    basePath: 'Modules/',
    modules: {},
    externalPath: "http://127.6.3.1:8000/xapi/UI/ModuleManager/get",
    bootDefaultVuexModules: async function () {
      await this.load('App', 'User').then(async function () {
        await $hkms.load('App', 'App')
        await $hkms.load('App', 'SideMenu')
      })
    },
    bootUserPermissions: function () {
      return new Promise((resolve, reject) => {
        let user = getLS('user')
        if (user && user.modules) {
          let userBackendModules = user.modules
          let localModules = $store.state['App-User-state'].modules
          let useLocalModules = $store.state['App-User-state'].useLocalModules
          this.loadNameSpacesModules((useLocalModules) ? localModules : userBackendModules).then(() => {
            $store.state['App-User-state'].isVerified = true
            resolve(true)
          })
        } else {
          resolve(false)
        }
      })
    },
    loadNameSpacesModules: async function (namespaces) {
      let c = this
      return new Promise(async function (resolve) {
        let promises = []
        for (let namespace in namespaces) {
          for (let module in namespaces[namespace]) {
            promises.push(c.load(namespace, namespaces[namespace][module].name))
          }
        }
        let result = await Promise.all(promises)
        resolve()
      })
    },
    loadExternalComponent:  (compJSONUrl, namespace = null, compName = null) => {
      return new Promise(async function (resolve) {
        let url = compJSONUrl
        if (!url) url
        fetch(compJSONUrl).then(res => res.text()).then(r => {
          let comp = Function(r)()
          if (comp.hasOwnProperty('data')) {
            let compData = comp.data
            comp.data = () => {
              return compData
            }
          }

          if (namespace && compName)

          if (compName) $vue.component(compName, comp);
          resolve(comp);
        })
      })
    },
    loadExternalModule: async function (moduleNamespace, moduleData) {
      return new Promise(async (resolve, reject) => {
        let moduleName = moduleData.name
        let modulePath = this.externalPath + "/" +  moduleNamespace + '/' + moduleName
        let fullModule = null
        await fetch(modulePath).then(response => response.json()).then(data => fullModule = data)
        let module = Function(fullModule.index)()

        if (!module.info.hasOwnProperty('viewGroup')) module.info.viewGroup = moduleData.group_name
        if (typeof this.modules[moduleNamespace] != "object") this.modules[moduleNamespace] = {}
        this.modules[moduleNamespace][moduleName] = module

        if (module.hasOwnProperty('routes')) {
          let moduleRoutes = module.routes

          let routes  = router.options.routes
          let routeData = routes.find(r => r.path === module.mainRoute)
          if (routeData) {
            routeData.children =  moduleRoutes
            router.addRoute(routeData)
          }
        }

        this.loadModuleStore(module).then( () => {
          resolve()
        })
      })
    },
    load: async function (moduleNamespace, moduleName) {
      return new Promise(async (resolve, reject) => {
        let modulePath = this.basePath + moduleNamespace + '/' + moduleName
        let { module } = await import('@/' + modulePath + '/index.js')
        /*
          !!! THIS WAS A TESTING FOR THE POSSIBILITY OF CONFIGURING FRONTEND MODULE FROM THE BACKEND !!!

          module.caption = 'HELLO IT Is ME'
          console.log('loading Module', module.caption)
        */

        if (typeof this.modules[moduleNamespace] != "object") {
          this.modules[moduleNamespace] = {}
        }
        this.modules[moduleNamespace][moduleName] = module
        if (!module.hasOwnProperty('noRoutes')) {
          let {default: moduleRoutes} = await import('@/' + modulePath+ '/routes.js')

          let routes  = this.router.options.routes
          let routeData = routes.find(r => r.path === module.mainRoute)
          if (routeData) {
            this.router.addRoute(moduleRoutes)
          }
        }

        this.loadModuleStore(module).then( () => {
          resolve()
        })
      })
    },
    loadModuleStore: function (module) {
      return new Promise(async (resolve, reject) => {
        let store = this.store
        let namespace = module.info.namespace;
        let modulePath = this.basePath + namespace + '/' + module.info.name
        let moduleName = module.info.name
        let vuexModules = module.vuexModules
        let moduleNameToRegister = null
        if (vuexModules.length === 0) resolve()
        for (let i = 0; i < vuexModules.length; i++) {
          let vuexModule = vuexModules[i]
          moduleNameToRegister = namespace + '-' + moduleName + '-' + vuexModule//this.kebabToCamelCase(moduleName + '-' + vuexModule)
          if (!store.hasModule(moduleNameToRegister)) {
            let { default: importedModule } = await import('@/' + modulePath + '/store/' + vuexModule + '.js')
            store.registerModule(moduleNameToRegister, importedModule)

            //this.modules[namespace][module.info.name].state = store.state[namespace + '-' + moduleName + '-state']
            resolve()
          } else {
            resolve()
          }
        }
      })
    },
    vuexAction: function (params) {
      return new Promise((resolve, reject) => {
        $store.dispatch(params.moduleName + '-' + params.vuexModule + '/' + params.action, params.data || {}).then( r => {
          resolve(r)
        }).catch( e => {
          reject(e)
        })
      })
    },
    /*unloadVuexModule: (moduleName) => {
      let camelCase = this.kebabToCamelCase(moduleName)
      $store.unregisterModule(camelCase + 'API')
      $store.unregisterModule(camelCase + 'State')
    },*/
    kebabToCamelCase: (str) => {
      return str.split('-').map((item, index) => index ? item.charAt(0).toUpperCase() + item.slice(1).toLowerCase() : item).join('')
    }
  }
}
